#header {
    width: 100%;
    flex: 1;
    background-color: black;
    color: white;
    
    text-align: center;
    display: flex;
    flex-direction: row;
    justify-content: left;
}

#header > p {
    margin: 0 30px;
    padding: 0;
    border: 0;
    display: flex;
    align-items: center;
    font-size: 30px;
}

#header > a {
    text-decoration: none;
    color: white;
    font-size: 40px;
    display: flex;
    align-items: center;
}