#tab {
    flex: 1;
    /* width: 10%; */
    height: 100%;
    background-color: rgb(65, 65, 65);
    display: flex;
    flex-direction: column;
}

#tab > * {
    background-color: rgb(82, 82, 82);
    height: 5%;
    color: white;
    border-bottom: 1px black solid;
    display: flex;
    /* text-align: center; */
    align-items: center;
    justify-content: center;
}