#chart {
    flex: 9;
    /* width: 100%; */
    height: 100%;
    display: flex;
    flex-direction: column;
}

#chart > div {
    flex: 1;
    height: 100%;
    display: flex;
    color: white;
    align-items: center;
}

#chart > div > * {
    height: 100%;
    flex: 1;
    display: flex;
}

#chart div div {
    flex: 1;
    /* background-color: red; */
    position: relative;
    margin: auto;
    width: 50vw;
}